import React from 'react';
import { InfoWindow as GoogleInfoWindow } from 'react-google-maps';

const getGoogleMapUrl = (geometry) => {
  console.log('geometry');
  const baseUrl = 'https://www.google.com/maps/search/?api=1';
  return `${baseUrl}&query=${geometry.coordinates[1]},${geometry.coordinates[0]}`;
};

class InfoWindow extends React.Component {
  render () {
    const {
      geometry,
      properties
    } = this.props.marker;

    const googleMapUrl = getGoogleMapUrl(geometry);

    return (
      <GoogleInfoWindow onCloseClick={() => this.props.handleMarkerClose(this.props.marker)}>
        <div
          style={{
            width: `200px`,
          }}
        >
          <h3>{properties.name}</h3>
          {
            !!properties.description && (
              <div
                style={{
                  lineHeight: `20px`,
                }}
                dangerouslySetInnerHTML={{__html: properties.description}}
              />
            )
          }
          <a href={googleMapUrl}>Open in Google maps</a>
        </div>
      </GoogleInfoWindow>
    );
  }
}

InfoWindow.propTypes = {
  handleMarkerClose: React.PropTypes.func,
  marker: React.PropTypes.object,
};

export default InfoWindow



